.services__title {
  width: 120px;
  text-align: center;
  margin: 0 auto;
  font-size: 19px;
  font-family: "Kaushan Script" sans-serif;
  font-weight: 400;
  font-style: italic;
  color: #333333;
  margin-bottom: 17px;
  text-transform: capitalize; }

.services__subtitle {
  width: 256px;
  text-align: center;
  margin: 0 auto;
  font-size: 24px;
  font-family: "Montserrat";
  font-weight: 700;
  font-style: normal;
  color: #333333;
  margin-bottom: 70px;
  text-transform: uppercase;
  position: relative; }

.services__subtitle:after {
  position: absolute;
  display: block;
  content: "";
  width: 60px;
  height: 3px;
  background-color: #f38181;
  left: 95px;
  top: 55px; }

.services__list {
  width: 960px;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }

.services__listItem {
  width: 248px;
  position: relative;
  padding-left: 48px;
  margin-bottom: 70px; }
  .services__listItemSvg {
    position: absolute;
    display: block;
    top: 0;
    left: 0; }
  .services__listItemTitle {
    font-size: 14px;
    font-family: "Montserrat";
    font-weight: 400;
    font-style: normal;
    color: #333333;
    margin-bottom: 16px;
    text-transform: uppercase; }
  .services__listItemAbout {
    font-size: 12px;
    font-family: "Montserrat";
    font-weight: 400;
    font-style: normal;
    color: #b6b6b6; }
